class BaseClass{
	constructor(str){	//通过给到的CSS选择器来创建该DOM对应的zepto对象
		this.$dom = $(str);
		this.$dom.hide();
		this.init();
		
	}
	//默认的初始化函数
	init(){
		
	}
	//默认的显示该DOM的函数
	show(){
		this.$dom.show();
	
	}
	//默认的隐藏该DOM的函数
	hide(){
		this.$dom.hide();
	}
}

module.exports = BaseClass;


