//弹窗基类
class BasePopupClass extends BaseClass{
	constructor(str){  //通过css选择器来构建zepto对象
		super(str);
		viewAdapt.push('.popup .box',342/523);
		// this.init();
	}
	//弹窗默认的初始化
	init(){
		
	}
	//默认的显示方式
	show(){
//		super.show();
		this.$dom.fadeInUp();
	}
	//默认的隐藏方式
	hide(){
//		super.hide();
		this.$dom.fadeOutDown();
	}
}

module.exports = BasePopupClass;


